.card {
  /* border: 9px solid #999; */
  margin: 9vh;
  width: auto;
  min-width: 300px;
  display: flex;
  border-radius: 12px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 5);
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
}

img {
  width: 7.5rem;
  padding: 5px;
}
